.benefits {
  background-color: $color-gamma;
  padding-bottom: 40px;

  &__label {
    position: relative;
    top: -50px;
    width: 110px;
    margin-bottom: -30px;
    margin-left: auto;
    margin-right: auto;
  }

  &__price {
    color: $color-psi;
    text-align: center;

    &-name {
      letter-spacing: -0.5px;
    }

    &-value {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-family: $font-roboto-slab;
      line-height: 1;
      letter-spacing: -2px;

      svg {
        margin-left: 9px;
      }
    }

    &-item + &-item {
      margin-top: 20px;
    }
  }

  &__price-full {
    opacity: .4;

    .benefits__price-name {
      font-size: 1rem;
    }

    .benefits__price-value {
      position: relative;
      font-size: 36px;
      letter-spacing: -2px;

      &::before {
        position: absolute;
        top: 50%;
        right: -2px;
        left: -2px;
        transform: translateY(-50%);
        height: 2px;
        background-color: $color-psi;
        content: '';
      }
    }
  }

  &__price-part,
  &__price-limit {
    display: flex;
    flex-direction: column;

    .benefits__price-value {
      order: -1;
    }
  }

  &__price-part {
    .benefits__price-name {
      font-size: 18px;
    }

    .benefits__price-value {
      font-size: 56px;
    }
  }

  &__price-limit {
    .benefits__price-value {
      font-size: 40px;
    }
  }

  //
  &__label + &__price {
    margin-top: 1rem;
  }

  //
  @media (min-width: $device-middle) {
    padding-bottom: 70px;

    &__price {
      &-item + &-item {
        margin-top: 34px;
      }
    }

    &__price-full {
      .benefits__price-value {
        font-size: 38px;
      }
    }

    &__price-part {
      .benefits__price-name {
        font-size: 20px;
      }
      .benefits__price-value {
        font-size: 80px;
      }
    }

    &__price-limit {
      .benefits__price-name {
        font-size: 18px;
      }
      .benefits__price-value {
        font-size: 50px;
      }
    }

    &__label + &__price {
      margin-top: 2rem;
    }
  }

  @media (min-width: $device-large) {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__label {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
