.check {
  display: flex;
  align-items: baseline;
  font-family: $font-open-sans;
  font-size: 14px;
  color: $clr-second;
  cursor: pointer;

  &__input:checked ~ &__box {
    .check__box-icon {
      display: block;
    }
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    border: 2px solid $border-color;

    &-icon {
      display: none;
    }
  }

  &__text {

  }

  //
  &__box + &__text {
    margin-left: 20px;
  }
}
