.form {
  &__group {
    & + & {
      margin-top: 20px;
    }
  }

  &__check {
    margin-top: 20px;
  }

  &__button {
    margin-top: 48px;
  }

  //
  @media (min-width: $device-middle) {
    &__button {
      margin-top: 30px;
    }
  }

  @media (min-width: $device-large) {
    display: flex;
    flex-direction: column;

    &__check {
      order: 1;
      margin-top: 30px;
    }

    &__button {
      margin-top: 20px;
    }
  }
}
