.list {
  font-family: $font-open-sans;
  color: $clr-second;

  &__item {

  }

  //
  &__item + &__item {
    margin-top: 12px;
  }

  //
  &--marker {
    .list {
      &__item {
        position: relative;
        padding-left: 22px;

        &::before {
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: $color-gamma;
          content: '';
        }
      }
    }
  }

  //
  @media (min-width: $device-middle) {
    &--info {
      display: flex;
      justify-content: center;
      margin: -25px;

      .list__item {
        margin: 25px;
      }
    }
  }

  @media (min-width: $device-large) {
    &--info {
      justify-content: flex-start;
      margin: -17px;
      font-size: 20px;

      .list__item {
        margin: 17px;
      }
    }
  }
}
