.label {
  position: relative;
  padding: 20px 0 5px;
  background-color: $label-bg;
  color: $label-color;
  text-align: center;

  // arrow label
  &::after {
    position: absolute;
    left: 0;
    bottom: -20px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px 55px 0 55px;
    border-color: $label-bg transparent transparent transparent;
    content: '';
  }

  &__title {
    font-size: 14px;
    letter-spacing: 1px;
    text-transform: lowercase;
  }

  &__value,
  &__descr {
    letter-spacing: -0.5px;
  }

  &__value {
    font-size: 30px;
    line-height: 1;
  }

  &__descr {
    font-size: 1rem;
    opacity: .7;
  }

  //
  &__title + &__value {
    margin-top: 5px;
  }

  &__value + &__descr {
    margin-top: 3px;
  }

  //
  @media (min-width: $device-large) {

  }
}
