
// css variables
:root {

}

// scss var
$base-font-family: 15px;
$font-default: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;

// fonts
$font-roboto-bold: 'Roboto-Bold', sans-serif;
$font-roboto: 'Roboto', sans-serif;

$font-roboto-slab: 'RobotoSlab', sans-serif;

$font-open-sans-bold: 'OpenSans-SemiBold', sans-serif;
$font-open-sans: 'OpenSans', sans-serif;



// colors
$color-alpha: #000;
$color-beta: #657195;
$color-gamma: #3D3BFF;
$color-psi: #fff;
$color-omega: #E6E8F0;

$clr-default: $color-alpha;
$clr-second: $color-beta;
$clr-accent: $color-gamma;
$clr-reverse: $color-psi;

$border-color: $color-omega;

$label-bg: $color-alpha;
$label-color: $color-psi;

$box-shadow: 0px 2px 24px rgba(168, 175, 196, 0.3);


// breakpoint
$device-big: 1440px;

$device-large: 1024px;
$max-device-large: 1439px;

$device-middle: 768px;
$max-device-middle: 1023px;

$device-small: 375px;
$max-device-small: 767px;
