
html {
  height: 100%;
  font-size: $base-font-family;
  line-height: 1.4;
  letter-spacing: -0.5px;
}

body {
  min-height: 100%;
  font-family: $font-default;
  color: $clr-default;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}

h1 {
  font-family: $font-roboto-bold;
  line-height: 1.2;
  letter-spacing: -0.5px;
}

h2 {
  font-family: $font-roboto-bold;
  line-height: 1.2;
  letter-spacing: -0.5px;
}

.ff-roboto-b {
  font-family: $font-roboto-bold;
}

.ff-roboto {
  font-family: $font-roboto;
}

.ff-roboto-s {
  font-family: $font-roboto-slab;
}

.ff-open-b {
  font-family: $font-open-sans-bold;
}

.ff-open {
  font-family: $font-open-sans;
}

.container {
  padding: 0 25px;

  @media (min-width: 1220px) {
    max-width: 1170px;
    margin: 0 auto;
  }
}

.container-inner {
  padding: 0 1rem;

  @media all and (min-width: 1220px) {
    padding: 0 calc(50% - 585px);
  }
}

.hide {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
