.box {
  &__left {
    margin: 0 -25px;
  }

  &__right {
    max-width: 464px;
    margin: 40px auto;
  }

  &__title {
    @include font-size(30);
    margin-bottom: 20px;
  }

  &__info {
    b {
      color: $clr-accent;
    }
  }

  //
  &__list + &__list,
  &__list + &__form {
    margin-top: 30px;
  }

  //
  @media (min-width: $device-middle) {
    &__title {
      @include font-size(38);
      text-align: center;
    }

    &__left {
      margin: 0;
    }
  }

  @media (min-width: $device-large) {
    display: flex;
    box-shadow: $box-shadow;
    background-color: #FAFBFE;

    &__left,
    &__right {
      flex: 1 0 50%;
      max-width: 50%;
    }

    &__right {
      padding: 60px 85px;
    }

    &__title {
      font-size: 34px;
      text-align: left;
    }
  }
}
